html {
	height: 100%;
    body {
		height: 100%;
    }
}

#app {
    display: grid;
    grid-template-columns: 30% 70%;
    grid-template-rows: 100%;
    grid-template-areas:
        "header header"
        "left right"
    ;
    gap: 10px;
}

.player-info {
    border: 1px solid #000;
    margin: 6px;
    padding: 6px;
    grid-area: header;
}

.players-list {
    border: 1px solid #000;
    margin: 6px;
    padding: 6px;
    grid-area: left;
}

.game-block {
    border: 1px solid #000;
    margin: 6px;
    padding: 6px;
    grid-area: right;
}

.status
{
    color: black;
    
    &--online {
        color: green;
    }

    &--offline {
        color: red;
    }
}